<template>
    <div class="notifys">
        <transition-group name="list" tag="div">
            <div v-for="(notification) in notifications" :key="notification.is">
                <div class="notify" :class="notification.is +'-bg'" :style="{
                        'background': 'linear-gradient(135deg, rgba(0, 0, 0, 0.798) 0%, ' + notification.colors[notification.is].bg + ', rgba(0, 0, 0, 0.644) 100%)'
                    }">

                    <div class="icon">
                        <Icon :icon="notification.icon" :color="notification.colors[notification.is].icon" />
                    </div>
                    <div class="content">
                        <p>{{ notification.title }}</p>
                        <div class="text">{{ notification.message }}</div>
                    </div>
        
                    <div class="progress-bar">
                        <div class="progress" 
                        :class="notification.is +'-bar'" 
                        :style="{'animation-duration': notification.duration + 'ms', 'background' : notification.colors[notification.is].bar}"
                        ></div>
                    </div>
                </div>
            </div>
        </transition-group>

    </div>
</template>

<script>
import { Icon } from '@iconify/vue';

export default {
    name: 'notifications-component',
    components: {
        Icon
    },
    data() {
        return {
            icons: {
                // police: 'map:police',
                // essen: 'mdi:food',
                // trinken: 'mdi:cup-water',
                // success: 'mdi:check-circle-outline',
                // error: 'mdi:alert-circle-outline',
                // warning: 'mdi:alert-octagon-outline',
                // info: 'mdi:information-outline'
            },
            colorBg: {
                // police: {
                //     bg: 'rgba(50, 105, 255)',
                //     bar: '#3262ff',
                //     icon: "lightblue"
                // },
                // essen: {
                //     bg: 'rgba(255, 165, 0)',
                //     bar: '#ff0000',
                //     icon: "orange"
                // },
                // trinken: {
                //     bg: 'rgba(172, 190, 216)',
                //     bar: '#ACBED8',
                //     icon: "#ACBED8"

                // },
                // success: {
                //     bg: 'rgba(53, 255, 71)',
                //     bar: '#35FF69',
                //     icon: "#35FF69"

                // },
                // error: {
                //     bg: 'rgba(144, 11, 21)',
                //     bar: 'red',
                //     icon: "red"

                // },
                // warning: {
                //     bg: 'rgba(255, 255, 0)',
                //     bar: '#ffff00',
                //     icon: "yellow"

                // },
                // info: {
                //     bg: 'rgba(89, 89, 89)',
                //     bar: '#A5A5A5',
                //     icon: "CCCCCC"
                // }
            },
            notifications: []
        };
    },
    methods: {
        message(evt) {
            const { action, ...data } = evt.data;

            const actions = {
                'setNotifys': () => {
                    this.Notifys(data.type, data.label, data.message, data.duration);
                },
                'extraData': () => {
                    this.icons = data.icons;
                    this.colorBg = data.colorBg;
                },
            };

            const actionFunction = actions[action];
            if (actionFunction) {
                actionFunction();
            }
        },
        Notifys(is, title, message, duration) {
            const data = {
                is: is,
                title: title,
                icon: this.icons[is],
                message: message,
                duration: duration,
                colors: this.colorBg
            }

            this.notifications.unshift(data)
            
            setTimeout(() => {
                const index = this.notifications.indexOf(data)

                if (index !== -1) {
                    this.notifications.splice(index, 1);
                }
            }, duration + 300);
        },

    },
    mounted() {
        window.addEventListener('message', this.message)
    },
    unmounted() {
        window.removeEventListener('message', this.message)
    },
};
</script>

<style lang="scss" scoped>
.notifys {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 45vh;
    max-height: 45vh;
    overflow: hidden;
    right: 1vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    font-family: 'SF Pro Display', sans-serif;

    .notify {
        position: relative;
        height: fit-content;
        width: 24vh;
        max-width: 24vh;
        
        display: flex;
        align-items: center;
        float: right;
        margin-top: .8519vh;
        padding: .25vh;
        border-radius: .5vh;
        transition: all .5s ease;
        .icon {
            margin-right: .5vh;
            margin-left: .75vh;
            color: rgba(255, 255, 255, 0.772);
            font-size: 2.4vh;
        }

        .content {
            flex-grow: 1;
            color: white; 
            margin-bottom: 0;
            margin-top: 0;

            p {
                margin-top: .5vh;
                margin-bottom: 0;
                text-shadow: 0.0926vh 0.0926vh 0.2778vh rgba(0, 0, 0, 0.5);
                font-size: 1.5vh;
            }

            .text {
                margin-top: 0;
                padding: 0;
                padding-right: 1vh;
                font-size: 1.2vh; 
                color: rgba(255, 255, 255, 0.531);
                text-shadow: 0.0926vh 0.0926vh 0.1778vh rgb(0, 0, 0);
                height: fit-content;
            }
            margin-bottom: .5vh;
        }
        .progress-bar {
            position: absolute;
            margin: auto;
            width: 100%;
            height: 0.37vh;
            bottom: 0;
            left: 0;
            margin-top: 0.93vh;
            border-radius: .5vh;

            background: rgba(0, 0, 0, 0.36);

            .progress {
                position: relative;
                width: 0%;
                height: 100%;
                left: 0vh;
                border-radius: .5vh;
                border-bottom-left-radius: .5vh;
                border-bottom-right-radius: .5vh;

                animation-name: width;
                animation-iteration-count: normal;
                animation-timing-function: linear;
            }
        }
    }
}

@keyframes width {
  from {
    width: 100%
  }
  to {
    width: 0%
  }
}

.list-move,
.list-enter-active,
.list-leave-active {
    transition: all .5s ease;
}

.list-enter-from,
.list-leave-to {
    opacity: 0;
    transform: translateX(-2.7778vh);
}

.list-leave-active {
    position: absolute;
}



</style>
