          <svg class="dark" width="43" height="19" viewBox="0 0 43 19" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path :class="{ 'animated-path': true, 'animate': vehicle.nos, 'reverse-animate': !vehicle.nos }" fill-rule="evenodd" clip-rule="evenodd" d="M37.0399 1.53519V3.07624L22.4459 3.08064L7.85189 3.08505L7.84311 5.49967L7.83434 7.91431L6.64603 5.49967L5.45774 3.08505L2.72886 3.0806L0 3.07615V11.0381V19 H2.69333H5.38666L5.3881 16.5235L5.38955 14.0469L6.61955 16.5235L7.84955 19H10.5484H13.2473V13.2199V7.43989H25.1436H37.0399V8.95455C37.0399 9.90793 37.046 10.467 37.0566 10.4633C37.0734 10.4575 42.4778 5.32853 42.5374 5.26191C42.5689 5.22665 42.5343 5.19292 39.8214 2.61603C38.31 1.18045 37.0659 0.00323809 37.0566 1.8153e-05C37.046 -0.00369446 37.0399 0.562241 37.0399 1.53519ZM13.9886 13.6818L13.993 18.9736H19.3358H24.6786L24.683 13.6818L24.6874 8.39004H19.3358H13.9842L13.9886 13.6818ZM25.6875 11.7243V15.0585L28.7186 15.063L31.7497 15.0675L31.7543 15.6173L31.759 16.1672H28.7232H25.6875V17.5748V18.9824H30.9952H36.3029V15.6041V12.2258H33.1534H30.0039V11.7595V11.2933H33.1358H36.2678V9.84165V8.39004H30.9777H25.6875V11.7243ZM20.2833 13.7302V16.1672H19.3446H18.4059V13.7302V11.2933H19.3446H20.2833V13.7302Z" fill="white" fill-opacity="0.15"/>
          </svg>          
        </div>

        <div class="vehicle-infos">
          <div class="info" v-for="(info, index) in vehicle.infos" :key="index">
            <Icon class="icon" :icon="info.icon" :class="{'active': info.is}"/>
          </div>
        </div>

      </div>
    </div>
</template>

<script>
import { Icon } from '@iconify/vue';

export default {
    name: 'speedo-meter',
    components: {
        Icon
    },
    data() {
        return {
            vehicle: {
                kmh: 0,
                nos: true,
                fuel: {
                  level: 100,
                  max: 100,
                },
                infos: [
                  {
                    is: false,
                    icon: "mdi:seatbelt"
                  },
                  {
                    is: false,
                    icon: "ph:engine-fill"
                  },
                  {
                    is: false,
                    icon: "game-icons:car-door"
                  },
                ]
            }
        };
    },
    methods : {
        message(evt) {
          const { action, ...data } = evt.data;

          const actions = {
            'setSpeed': () => {
              this.vehicle.kmh = data.speed;
              this.setNeedle()
            },
            'setVehStates': () => {
              this.vehicle.infos[0].is = data.seatbelt;
              this.vehicle.infos[1].is = data.engine;
              this.vehicle.infos[2].is = data.locked;
            },
            'setVehFuel': () => {
              this.vehicle.infos
              this.setFuelProgress(this.vehicle.fuel.level)
            },

          };

          const actionFunction = actions[action];
          if (actionFunction) {
              actionFunction();
          }
        },
        animate() {
            const path = document.querySelector('.animated-path');
            path.classList.add('animate');
            path.classList.remove('reverse-animate');
        },
        reverseAnimate() {
            const path = document.querySelector('.animated-path');
            path.classList.add('reverse-animate');
            path.classList.remove('animate');
        },
        setFuelProgress(current) {
          const circle = document.getElementById('fuel-progress');
          const max = this.vehicle.fuel.max;

          let progress = current / max; 
          let offsetSpeed = 796 - progress * 140;

          if (offsetSpeed < 656) {
            circle.style.strokeDashoffset = 656;
          } else {
            circle.style.strokeDashoffset = offsetSpeed;
          }
        },
        setNeedle() {
            let startDeg = 236.19; // Start Degree geht von 236 auf 0 runter und dann soll es erst auf den End Deg schalten heißt erst ist quasi der endDeg bei 0 Deg solange der Vehicle Kmh bis 150 ist
            let endDeg = 120.89; // Degree ende wie es Maximal sein darf
            let maxKmh = 300
            let progress = this.vehicle.kmh / maxKmh; // Annahme: 300 ist der Maximalwert für kmh
            let progress2 = this.vehicle.kmh / (maxKmh / 2); // Annahme: 300 ist der Maximalwert für kmh

            const needle = document.querySelector('.needle'); 
            let currentDeg;


            if (this.vehicle.kmh >= 150) {
                if (progress <= 0.5) {
                // Bereich von startDeg bis 0 Grad
                currentDeg = startDeg * (1 - progress * 2); 
                } else {
                // Bereich von 0 Grad bis endDeg
                progress = (progress - 0.5) * 2;
                currentDeg = endDeg * progress;
                }
            } else {
                // Bereich von startDeg bis 359 Grad
                let range = 359 - startDeg;
                currentDeg = startDeg + (progress2 * range);
            }

            if (needle) {
                needle.style.transform = `translate(-50%, -50%) rotate(${currentDeg}deg)`;
            }
            // console.log(currentDeg, progress)
        },
        needleCheck() {
          let kmh = 0;
          let direction = 1; // Richtung: 1 für Hochzählen, -1 für Runterzählen
          const interval = setInterval(() => {
              if (kmh <= 300 && kmh >= 0) {
                  this.vehicle.kmh = kmh;
                  this.setNeedle();
                  kmh += direction;
              } else {
                  direction *= -1; // Richtung drehen
                  kmh += direction * 2; 
              }

              if (kmh < 0) {
                this.vehicle.nos = false
                clearInterval(interval); 
              }

          }, 5);
        },
        fuelCheck() {
          let fuelLevel = 0;
          let direction = 1; // Richtung: 1 für Hochzählen, -1 für Runterzählen
          const interval = setInterval(() => {
              if (fuelLevel >= 0 && fuelLevel <= 100) {
                  this.setFuelProgress(fuelLevel);
                  fuelLevel += direction;
              } else {
                  direction *= -1; // Richtung umkehren
                  fuelLevel += direction * 2; // Kraftstoffniveau entsprechend anpassen
              }

              this.vehicle.fuel.level = fuelLevel

              if (fuelLevel < 0) {
                this.vehicle.fuel.level = 0
                  clearInterval(interval); // clearInterval aufrufen, wenn der Treibstoffpegel unter 0 fällt
              }
          }, 25); // Intervallzeit in Millisekunden
        }

    },
  watch: {
    'vehicle.nos': function(newValue, oldValue) {
      if (newValue !== oldValue) {
        if (newValue) {
          this.animate();
        } else {
          this.reverseAnimate();
        }
      }
    }
  },
  mounted() {
    window.addEventListener('message', this.message)

  },
  unmounted() {
    window.removeEventListener('message', this.message)
  }
};
</script>

<style lang="scss" scoped>

