        svg {
          position: absolute;
        }
      }

    }

      .needle {
        position: absolute;
        width: 256px;
        height: 150px;
        top: 26.5%;
        left: 50.15%; 
        transform: translate(-50%, -50%); 
        transform-origin: bottom center; 

        svg {
          position: absolute;
          width: 45px;
          height: 97px;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);  
          transition: transform 0.5s ease; 

        }
    }
}

  @keyframes fillAnimation {
  0% {
      fill: #ffffff2f;
      fill-opacity: .15;
      stroke-width: 0;
  }
  100% {
      fill: $server-color;
      fill-opacity: 1; 
      stroke-width: 0;
  }
}

.animated-path {
  transform-origin: right center;
}

.animate {
  animation: fillAnimation 1s linear forwards;
}

.reverse-animate {
  animation: fillAnimationReverse 1s linear forwards;
}

@keyframes fillAnimationReverse {
  0% {
    fill: $server-color; 
    fill-opacity: 1;
    stroke-width: 0;
  }
  100% {
    fill: #ffffff8e;
    fill-opacity: .15;
    stroke-width: 0;
  }
}
</style>
