<template>
    <div class="postit">
        <!-- Header Bereich -->
        <div class="postit-header">
            <h2>POST-IT</h2>
            <span>Gebe eine Nachricht ein</span>
        </div>

        <!-- Eingabefelder Bereich -->
        <div class="postit-inputs">
            <textarea v-model="message" class="postit-eingabe" maxlength="50" name="nachricht" placeholder="Nachricht eingeben..."></textarea>
            <input v-model.number="distance" type="number" class="postit-eingabe distance" name="distance" maxlength="2" min="3" max="15" placeholder="3-15m" pattern="[0-9]*" @input="updateDistance">
        </div>

        <!-- Buttons Bereich -->
        <div class="postit-buttons">
            <button class="postit-button cancel-button" @click="resetFields">Abbrechen</button>
            <button class="postit-button send-button" @click="sendMessage">Senden</button>
        </div>
    </div>
</template>


<script>
// import { Icon } from '@iconify/vue';

export default {
    name: 'postit-component',
    // components: {
    //     Icon
    // },
    data() {
        return {
            message: '',
            distance: null
        };
    },
    methods: {
        resetFields() {
            this.message = '';
            this.distance = null;
            this.$emit('hidePostIT')
            const fetch = {
                file: "postIt",
                label: "hidePostIT",
            }
            this.fetchToClient(fetch)
        },
        sendMessage() {
            console.log("Nachricht:", this.message);
            console.log("Distanz:", this.distance);
            
            if (this.distance < 3) {
                this.distance = 3;
            }

            const fetch = {
                file: "postIt",
                label: "sendPostIT",
                message: this.message,
                distance: this.distance
            }
            this.fetchToClient(fetch)
            this.$emit('hidePostIT')
        },
        updateDistance() {
            if (this.distance !== null && (this.distance.length > 2 || this.distance > 15)) {
                if (this.distance > 15) {
                    this.distance = 15;
                }

                if (this.distance.length >= 3) {
                    this.distance = this.distance.slice(0, 2); // Begrenzt die Länge auf 2 Ziffern
                }
            }
            console.log(this.distance)
        },
        fetchToClient(data) {
            /*global GetParentResourceName*/
            /*eslint no-undef: "error"*/
            fetch(`https://${GetParentResourceName()}/hud_sonix:fetchToClient`, {
                method: "post",
                body: JSON.stringify({
                    data: data,
                })
            }) 
        },
    }
};
</script>

<style lang="scss" scoped>
@import url('https://fonts.cdnfonts.com/css/pf-din-text-cond-pro-2');

.postit {
    background-color: #000000af;
    font-family: 'PF Din Text Cond Pro', sans-serif;

    border-radius: 0.7407vh;
    width: 29.7778vh; 
    &-header {
        text-align: center;
        margin-bottom: 1.8519vh;
        // border: 2px solid;
        margin-top: 3vh;
        h2, span {
            margin: 0;
            padding: 0;
        }

        h2 {
            color: white;
        }

        span {
            color: rgba(255, 255, 255, 0.288);
        }
    }

    &-inputs {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1vh;

        input, textarea {
            margin-bottom: 10px;
            border: .1vh solid rgb(48, 48, 48);
            background-color: rgba(0, 0, 0, 0.384);
            color: white;
            width: 80%;
            max-width: 80%;
            min-width: 80%;
            border-radius: .5vh;
            text-align: center;

            &:nth-child(1) {
                height: 10vh;
                min-height: 10vh;
                max-height: 10vh;
                padding: .55vh;
                resize: none;
            }


            &:nth-child(2) {
                height: 1.5vh;
                margin: auto;
                padding: .55vh;
            }

            &::placeholder {
                // padding: 1vh;
                color: rgba(255, 255, 255, 0.466);
            }
            
        }
    }

    &-buttons {
        position: relative;
        bottom: 0;
        display: flex;
        // justify-content: space-between;
        width: 100%;
        margin-top: 2vh;
        // background-color: #000000a4;
        // border-bottom-left-radius: .25vh;
        // border-bottom-right-radius: .25vh;
        
        button {
            flex: 1;
            border: none;
            background-color: #00000046;
            color: #fff;
            transition: background-color 0.3s;
            padding: 2vh;


            &:nth-child(1) {
                border-bottom-left-radius: .5vh;
                // border-bottom-right-radius: .5vh;

            }

            &:nth-child(2) {
                // border-bottom-left-radius: .5vh;
                border-bottom-right-radius: .5vh;

            }

            &:hover {
                background-color: #0056b3;
                cursor: pointer;

                            
                &:nth-child(1) {
                    background-color: #b30000;
                }
            }
        }
    }
}


</style>
