<template>
    <Transition>
        <div class="modalWindow">
            <p>{{ label }}</p>
            <h2 v-html="formatMessage(message)"></h2>
            <div class="buttons">
                <button @click="reject">Ablehnen <strong>(U)</strong></button>
                <button @click="accept">Annehmen <strong>(Y)</strong></button>
            </div>
        </div>
    </Transition>
</template>


<script>

export default {
    name: 'modalWindow-component',
    data() {
        return {
            label: "Fraktions Einladung",
            message: "Du wurdest eingeladen der Fraktion [Test] beizutreten.",
            strong: "Ballas",
            event: "",
        };
    },
    methods: {
        formatMessage(message) {
            // Regulärer Ausdruck, um den Text innerhalb der eckigen Klammern zu extrahieren
            const regex = /\[([^[\]]+)\]/g;
            // Ersetze den Text innerhalb der eckigen Klammern durch denselben Text, jedoch eingefasst in <strong>-Tags
            return message.replace(regex, '<strong style="color: white;">'+ this.strong +'</strong>');
        },
        reject() {
            // Logik für Ablehnen hier einfügen
            console.log('Abgelehnt');
            this.$emit('hide')
            const fetch = {
                file: "modalWindow",
                label: "recjeted",
            }
            this.fetchToClient(fetch)
        },
        accept() {
            // Logik für Annehmen hier einfügen
            console.log('Angenommen');
            this.$emit('hide')
            const fetch = {
                file: "modalWindow",
                label: "accepted",
                strong: this.strong,
                event: this.event
            }
            this.fetchToClient(fetch)
        },
        handleKeyPress(event) {
            if (event.key === 'u' || event.key === 'U') {
                this.reject();
            } else if (event.key === 'y' || event.key === 'Y') {
                this.accept();
            }
        },
        fetchToClient(data) {
            /*global GetParentResourceName*/
            /*eslint no-undef: "error"*/
            fetch(`https://${GetParentResourceName()}/hud_sonix:fetchToClient`, {
                method: "post",
                body: JSON.stringify({
                    data: data,
                })
            }) 
        },
        listener(evt) {
            const { action, ...data } = evt.data;

            const actions = {
                'setModalWindow': () => {
                    this.label = data.label
                    this.message = data.message,
                    this.strong = data.strong;
                    this.event = data.event;
                    // this.Notifys(data.is, data.code, data.name, data.street, data.message, data.duration);
                },
            };

            const actionFunction = actions[action];
            if (actionFunction) {
                actionFunction();
            }
        },
    },
    mounted() {
        window.addEventListener('message', this.listener)
        window.addEventListener('keydown', this.handleKeyPress);
    },
    unmounted() {
        window.removeEventListener('keydown', this.handleKeyPress);
        window.removeEventListener('message', this.listener)
    }
};
</script>

<style lang="scss" scoped>
@import url('https://fonts.cdnfonts.com/css/spline-sans');
@import url('https://fonts.cdnfonts.com/css/akrobat');

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}

.modalWindow-enter-active, .modalWindow-leave-active {
    transition: opacity 0.5s;
}
.modalWindow-enter, .modalWindow-leave-to /* .modalWindow-leave-active in <2.1.8 */ {
    opacity: 0;
}

.modalWindow {
    position: relative;
    width: 35vh;
    height: 17vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0vh;
    background-color: rgba(0, 0, 0, 0.685);
    padding: 2vh;
    border-radius: .5vh;
    font-family: 'Spline Sans', sans-serif;

    p,h2 {
        margin: 0;
        padding: 0;
        text-align: center;
    }

    p {
        
        color: white;
        font-size: 2.5vh;
        text-transform: uppercase;
    }

    h2 {
        color:  rgba(255, 255, 255, 0.568);
        font-size: 1.8vh;
        width: fit-content;
        height: fit-content;
        max-height: 15vh;
        max-width: 25vh;
        font-family: 'Akrobat', sans-serif;
        font-weight: 400;
        margin-bottom: 3vh;
        
    }


    .buttons {
        display: flex;
        gap: 1vh;
        button {
            padding: 1.5vh;
            padding-left: 2.5vh;
            padding-right: 2.5vh;
            border-radius: .5vh;
            border: none;
            text-shadow: 0.0926vh 0.0926vh 0.2778vh rgba(0, 0, 0, 0.5);      
            transition: .2s all;
            &:nth-child(1) {
                background-color: rgba(0, 0, 0, 0.671);
                color: white;
            }
            &:nth-child(2) {
                background-color: rgb(24, 255, 139);
                color: white;
            }

            &:hover {
                transform: scale(1.05);
                cursor: pointer;
            }
        }
    }
}

</style>
