<template>
    <div class="progressbar" v-if="progressBars && Object.keys(progressBars).length > 0">
        <div class="rectangle"></div>
        <div class="content-boxes">
            <div class="outer-content">
                <svg class="progress-circle-bg" viewBox="0 0 100 100">
                    <circle cx="50" cy="50" r="45" fill="none" stroke="white" opacity=".5" stroke-width="4"/>
                </svg>

                <svg class="progress-circle" viewBox="0 0 100 100">
                    <circle cx="50" cy="50" r="45" fill="none" :stroke="progressBars.color" :stroke-width="5" :stroke-dasharray="progressBars.progress * 2.83 + ' 283'"/>
                </svg>
                <svg class="posCircle" ref="posCircle" viewBox="0 0 100 100">
                    <circle cx="50" cy="50" r="45" fill="none" stroke="white" :stroke-width="5" stroke-dashoffset="753" stroke-dasharray="783"/>
                </svg>

                <div class="inner-content2">
                    <p>{{ progressBars.timeGone }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'progressbar-component',
    data() {
        return {
            progressBars: {},
        };
    },

    methods: {
        formatProgress(progress) {
            if (typeof progress === 'number') {
                return progress.toFixed(0);
            } else {
                return progress;
            }
        },
        updateRotation() {
            if (!this.progressBars) return
            const startRotation1 = 234;
            const endRotation1 = 0;
            const startRotation2 = 47;
            const endRotation2 = 280;

            const progress = this.progressBars.progress;

            let rotation;

            if (progress >= 60) {
                rotation = startRotation1 - (progress / 62) * (startRotation1 - endRotation1);
            } else {
                rotation = startRotation2 - ((progress - 50) / 62) * (endRotation2 - startRotation2);
            }    

            const posCircle = document.querySelector('.posCircle');
            if (posCircle) {
                posCircle.style.transform = `rotate(${rotation}deg)`;
            }
        },
        pushProgressbar(duration, color) {
            const id = Date.now();
            const intervalDuration = 10; // Intervall in Millisekunden

            const data = {
                id: id,
                progress: 100,
                color: color,
                startTime: Date.now(),
                duration: duration * 1000 
            };

            this.progressBars = data;

            
            const interval = setInterval(() => {
                if (!this.progressBars) return;

                const elapsedTime = Date.now() - this.progressBars.startTime;
                const remainingTime = this.progressBars.duration - elapsedTime + 100;

                if (remainingTime <= 0) {
                    this.progressBars.progress = 0;
                    this.updateRotation();
                    this.progressBars = null;
                    clearInterval(interval);
                } else {
                    let progress = (remainingTime / this.progressBars.duration) * 100;
                    progress = Math.floor(progress); // Abrunden auf die nächstniedrigere ganze Zahl
                    this.progressBars.progress = progress;
                    this.updateRotation();
                    this.progressBars.timeGone = Math.floor(elapsedTime / 1000); // Berechnung der verstrichenen Zeit in Sekunden
                }
            }, intervalDuration);
        },
        message(evt) {
            const { action, ...data } = evt.data;

            const actions = {
                'setProgressbar': () => {
                    this.pushProgressbar(data.time, data.color)
                },
            };

            const actionFunction = actions[action];
            if (actionFunction) {
                actionFunction();
            }
        },
    },
    mounted() {
        window.addEventListener('message', this.message)
        // this.pushProgressbar('leben', 5, "#ff3030")
    },
    unmounted() {
        window.removeEventListener('message', this.message)
    },
    beforeUnmount() {
        clearInterval(this.intervalId);
    }
    
};
</script>

<style lang="scss" scoped>

.progressbar {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 10vh;
    height: 10vh;
    gap: 8vh;

    .rectangle {
      position: absolute;
      width: 100%;
      height: 150%;
      background: black;
      filter: blur(6vh);
    }

    .content-boxes {
        position: relative;
        height: 8vh;
        width: 8vh;
        
        .outer-content {
            position: absolute;

            height:  8.5vh;
            width: 8.5vh;
            padding: 0;
            margin: 0;

            .progress-circle-bg,
            .progress-circle {
                position: absolute;
                top: 0;
                left: 0;
                transform: rotate(-90deg) scaleY(-1);;
    
                circle {
                    fill: none;
                }
            }
    
            .progress-circle-bg circle {
                // stroke: #ccc;
                opacity: .5;
                stroke-width: 3;
            }
    
            .progress-circle circle {
                stroke-width: 4;
                stroke-linecap: round;
                transition: stroke-dasharray 0.3s ease;
            }
            
            .posCircle {
                position: absolute;
                top: 0;
                left: 0;
                transform: rotate(270deg);
            }
        }


        .inner-content2 {
            position: relative;
            width: 100%;
            height: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            // background: black;
            // padding: 1vh;
            border-radius: 100%;
            p {
                padding: 0;
                margin: 0;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                color: white;
                font-family: 'Gilroy-Bold', sans-serif;
                font-size: 2.5vh;
            }
        }
    }
}

</style>
