<template>

    <transition-group name="list" tag="div" class="itemNotify">
        <div v-for="(item, index) in items" :key="index">

            <div class="item">
                <div class="top">
                    <div class="icon">
                        <Icon :icon="item.type == 'added' ? 'ic:baseline-plus' : 'ic:baseline-minus'" />
                    </div>
                    <p>{{ item.label }}</p>
                </div>
                <div class="container">
                    <img  :src="getImageSrc(item.name)" alt="test">
                    <div class="count">
                        {{ item.count }}x
                    </div>
                </div>

            </div>
        </div>
    </transition-group>

</template>


<script>
import { Icon } from '@iconify/vue';

export default {
    name: 'postit-component',
    components: {
        Icon
    },
    data() {
        return {
            items: []
        };
    },
    methods: {
        pushItem(label, name, type, count) {
            let duration = 4000;
            const data = {
                label: label,
                name: name,
                type: type,
                count: count,
                duration: duration
            }

            this.items.unshift(data)
            setTimeout(() => {
                const index = this.items.indexOf(data)

                if (index !== -1) {
                    this.items.splice(index, 1);
                }
            }, duration + 300);
        },
        message(evt) {
            const { action, ...data } = evt.data;

            const actions = {
                'setItemNotify': () => {
                    this.pushItem(data.label, data.name, data.type, data.count)        

                    // this.Notifys(data.is, data.code, data.name, data.street, data.message, data.duration);
                },
            };

            const actionFunction = actions[action];
            if (actionFunction) {
                actionFunction();
            }
        },
        getImageSrc(index) {
            return ("img/" + index + ".png").toLowerCase()
        },
    },
    mounted() {
        // this.pushItem("Advanced Rifle DES sex", "advancedrifle", "added", 5)
        // this.pushItem("Assault Rifle", "assaultrifle", "removed", 5)        
        window.addEventListener('message', this.message)
    },
    unmounted() {
        window.removeEventListener('message', this.message)
    },
};
</script>

<style lang="scss" scoped>
@import url('https://fonts.cdnfonts.com/css/akrobat');

.list-move,
.list-enter-active,
.list-leave-active {
    transition: all 0.5s ease;
}

.list-enter-from,
.list-leave-to {
    opacity: 0;
    transform: translateX(-320px);
}

.list-leave-active {
    position: absolute;
}

.itemNotify {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 3vh;
    font-family: 'Akrobat', sans-serif;
    height: 15vh;
    
    .item {
        width: 15vh;
        height: 15vh;
        background-color: rgba(0, 0, 0, 0.89);
        border-radius: .5vh;
        .top {
            position: relative;
            top: 0;
            width: 100%;
            height: 3vh;
            border-top-right-radius: .5vh;
            border-top-left-radius: .5vh;
            background-color: rgba(255, 255, 255, 0.199);

            display: flex;
            // justify-content: space-between;
            align-items: center;    
            
            .icon {
                color: white;
                width: 4vh;
                height: 3vh;
                border-bottom-right-radius: .5vh;
                border-top-left-radius: .5vh;
                background-color: rgba(0, 0, 0, 0.432);
                display: flex;
                justify-content: center;
                align-items: center;
            }

            p {
                width: 100%;
                margin: 0;
                padding: 0;
                margin-left: .5vh;
                color: rgba(255, 255, 255, 0.308);
                
                font-family: 'Akrobat SemiBold', sans-serif;
                text-align: center;
                text-transform: uppercase;                
                
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
        .container {
            position: relative;
            height: 76%;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 10vh;
                height: 10vh;
                object-fit: contain;
            }

            .count {
                position: absolute;
                bottom: 1vh;
                right: 1vh;
                color: white;
            }
        }
    }
}
</style>
