<template>
    <div class="pinned-notifys">
        <transition-group name="list" tag="div">
            <div v-for="(notification) in notifications" :key="notification.is">
                <div class="notify" :class="notification.is +'-bg'">

                    <div class="icon">
                        <Icon :icon="notification.icon" />
                    </div>
                    <div class="content">
                        <p>{{ notification.title }}</p>
                        <div class="time">{{ formatTime(notification.remainingTime) }}</div>
                    </div>
        
                    <div class="progress-bar">
                        <div class="progress" 
                        :class="notification.is +'-bar'" 
                        :style="{'animation-duration': notification.duration + 'ms'}"
                        ></div>
                    </div>
                </div>
            </div>
        </transition-group>

    </div>
</template>

<script>
import { Icon } from '@iconify/vue';

export default {
    name: 'pinned-notifys',
    components: {
        Icon
    },
    data() {
        return {
            notifications: []
        };
    },

    methods: {
        formatTime(time) {
            const minutes = Math.floor(time / 60000); // Berechne die Minuten
            const seconds = Math.floor((time % 60000) / 1000); // Berechne die Sekunden
            return `${minutes < 10 ? '0' : ''}${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
        },
        updateTime() {
            this.notifications.forEach(notification => {
                notification.remainingTime -= 1000; // Reduziere die verbleibende Zeit um 1 Sekunde (1000 Millisekunden)
            });
        },
        pinnedNotifys(is, title, icon, duration) {
            const data = {
                is: is,
                title: title,
                icon: icon,
                duration: duration,
                remainingTime: duration
            }

            this.notifications.unshift(data)
            setInterval(this.updateTime, 1000);
            setTimeout(() => {
                const index = this.notifications.indexOf(data)

                if (index !== -1) {
                    this.notifications.splice(index, 1);
                }
            }, duration + 300);
        },
        message(evt) {
            const { action, ...data } = evt.data;

            const actions = {
                'setPinnedNotify': () => {
                    this.pinnedNotifys(data.type, data.label, data.message, data.duration);
                },
            };

            const actionFunction = actions[action];
            if (actionFunction) {
                actionFunction();
            }
        },
    },
    mounted() {
        window.addEventListener('message', this.message)
    },
    unmounted() {
        window.removeEventListener('message', this.message)
    },
};
</script>

<style lang="scss" scoped>
.pinned-notifys {
    position: absolute;
    top: 50%;
    left: 1vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'SF Pro Display', sans-serif;
    .notify {
        position: relative;
        display: flex;
        align-items: center;
        width: fit-content; 
        min-width: 14vh;
        margin-bottom: 20px;
        border-radius: .5vh;
        // background: blue;
        padding: .25vh;
        .icon {
            margin-right: .5vh;
            margin-left: .75vh;
            color: rgba(255, 255, 255, 0.772);
            font-size: 2.4vh;

        }

        .content {
            flex-grow: 1;
            color: white; 
            margin-bottom: 0;
            margin-top: 0;

            p {
                margin-top: .5vh;
                margin-bottom: 0;
                text-shadow: 0.0926vh 0.0926vh 0.2778vh rgba(0, 0, 0, 0.5);
                font-size: 1.5vh;
            }

            .time {
                margin-top: 0;
                padding: 0;
                font-size: 1.2vh; 
                color: rgba(255, 255, 255, 0.531);
                text-shadow: 0.0926vh 0.0926vh 0.1778vh rgb(0, 0, 0);
            }
            margin-bottom: .5vh;
        }
        .progress-bar {
            position: absolute;
            margin: auto;
            width: 100%;
            height: 0.37vh;
            bottom: 0;
            left: 0;
            margin-top: 0.93vh;

            background: rgba(0, 0, 0, 0.36);

            .progress {
                position: relative;
                width: 0%;
                height: 100%;
                left: 0vh;

                background: #D6FF32;
                animation-name: width;
                animation-iteration-count: normal;
                animation-timing-function: linear;
            }

            .luck-bar {
                background: #D6FF32!important;
            }

            .sleep-bar {
                background: #FF3D3D!important;
            }

            .info-bar {
                    background: #2DFF74!important;
            }
        }

        &.luck-bg {
            background: linear-gradient(135deg, rgba(108, 108, 108, 0.498) 5%, rgba(214, 255, 50, 1), rgba(102, 102, 102, 0.712) 97%)!important;
        }

        &.sleep-bg {
            background: linear-gradient(135deg, rgba(85, 85, 85, 0.726) 5%, rgba(255, 61, 61, 1), rgba(113, 113, 113, 0.644) 97%)!important;
        }

        &.info-bg {
            background: linear-gradient(135deg, rgba(90, 90, 90, 0.871) 5%, rgba(45, 255, 116, 1), rgba(100, 100, 100, 0.6) 97%)!important;
        }




    }
}

@keyframes width {
  from {
    width: 100%
  }
  to {
    width: 0%
  }
}

.list-move,
.list-enter-active,
.list-leave-active {
    transition: all 0.5s ease;
}

.list-enter-from,
.list-leave-to {
    opacity: 0;
    transform: translateX(-320px);
}

.list-leave-active {
    position: absolute;
}



</style>
