<template>
    <div class="settings">
        <p>Einstellungen</p>
        <div class="line"></div>
        <div v-for="(setting, index) in settings" :key="index" class="setting">
            <label :for="'setting_' + index">{{ setting.label }}:</label>
            <div v-if="setting.type === 'checkbox'">
                <input type="checkbox" :id="'setting_' + index" v-model="setting.value">
            </div>
            <div v-else-if="setting.type === 'select'" class="setting-select">
                <select class="selection" :id="'setting_' + index" v-model="setting.value">
                    <option v-for="(option, optionIndex) in setting.options" :key="optionIndex" :value="option.value">{{ option.label }}</option>
                </select>
            </div>
        </div>
    </div>
</template>
  
  <script>
  export default {
    name: 'SettingsComponent',
    data() {
      return {
        settings: [
          {
            label: 'Map anzeigen',
            type: 'checkbox',
            value: false
          },
          {
            label: 'Foodbar Position',
            type: 'select',
            value: 'bottom-left',
            options: [
              { label: 'Unten links', value: 'bottom-left' },
              { label: 'Unten rechts', value: 'bottom-right' },
              { label: 'Mitte links', value: 'middle-left' },
              { label: 'Mitte rechts', value: 'middle-right' },
            ]
          }
        ]
      };
    }
  };
  </script>
  
<style scoped lang="scss">

.settings {
    font-family: "Roboto", sans-serif;
    position: absolute;
    width: 25vw;
    height: 50vh;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #1d2229;
    color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

    .setting {
        margin-bottom: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: .2vh solid black;
        .setting-label {
            font-size: 1.2rem;
            flex: 1;
            margin-right: 10px; // Abstand zwischen Label und Eingabefeldern
            

            
        }
    }
    .setting-select {
        color: #f5f6fa;
        transition: all 0.4s;
        overflow: hidden;
        border: none;
        background-color: rgba(0, 0, 0, 0.842);
        border-radius: .5vh;
        select {
            background-color: transparent;
            border-radius: .5vh;
            border: none;
            color: #f5f6fa;
            order: 1;

            &:active, &:focus, &:hover {
                border: none;
            }

        }

        option {
            position: relative;
            background-color: rgba(0, 0, 0, 0.842);
            border-radius: 4px;
            color: #fff;
            padding: 8px;
            border-radius: 1.5vh;
            margin-bottom: 8px;
            border: none;

            order: 0;

            &:active, &:checked, &:focus {
                border: none;
                background-color: #007bff;
                color: #fff;
            }
        }


    }
}


</style>
  