<template>
    <div class="dispatch">
        <transition-group name="lista" tag="div">
            <div v-for="notification in notifications" :key="notification.is" class="notify">
                    <div class="container">
                        <div class="dispatch-code">
                            {{notification.code}}
                        </div>
                        <div class="ueberschrift">
                            {{notification.message}}
                        </div>
    
                        <div class="content">
                            <div class="name">
                                <Icon class="icon" icon="fluent:person-12-filled" />
                                <div class="content-text">
                                    <span>{{notification.person}}</span>
                                </div>
                            </div>
                            <div class="position-text">
                                <Icon class="icon" icon="fa:map-marker" />
                                <div class="content-text">
                                    <span>{{ notification.street }}</span>
                                    <span>{{ notification.timeStamp }}</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="infos-icons">
                        <Icon class="icon" icon="fa:map-marker" />
                        <Icon class="icon" icon="fluent:call-48-filled" />
                    </div>
            </div>
        </transition-group>

    </div>
</template>

<script>
import { Icon } from '@iconify/vue';

export default {
    name: 'notifications-component',
    components: {
        Icon
    },
    data() {
        return {
            notifications: []
        };
    },
    methods: {
        Notifys(is, code, name, street, message, duration) {
            const now = new Date();
            const hours = ('0' + now.getHours()).slice(-2); // Führende Null hinzufügen
            const minutes = ('0' + now.getMinutes()).slice(-2); // Führende Null hinzufügen

            const timeStamp = hours + ':' + minutes;

            const data = {
                is: is,
                code: code,
                person: name,
                message: message,
                street: street,
                duration: duration,
                timeStamp: timeStamp
            }

            this.notifications.unshift(data)

            setTimeout(() => {
                const index = this.notifications.indexOf(data)

                if (index !== -1) {
                    this.notifications.splice(index, 1);
                }
            }, duration + 300);
        },

        message(evt) {
            const { action, ...data } = evt.data;

            const actions = {
                'setDispatcha': () => {
                    this.Notifys(data.is, data.code, data.name, data.street, data.message, data.duration);
                },
            };

            const actionFunction = actions[action];
            if (actionFunction) {
                actionFunction();
            }
        },
    },
    mounted() {
        window.addEventListener('message', this.message)
    },
    unmounted() {
        window.removeEventListener('message', this.message)
    },
};
</script>

<style lang="scss" scoped>

.dispatch {
    display: flex;
    flex-direction: column;
    width: 27vh;

    .notify {
        position: relative;
        height: 12vh;
        width: 27vh;
        background-color: rgba(0, 0, 0, 0.712);
        z-index: -1;
        color: white;
    
        display: flex;
        align-items: center;
        margin-top: .8519vh;
        padding: .25vh;
        border-radius: .5vh;
        transition: all .5s ease;

        .container {
            position: relative;
            width: 84%;
            height: 99%;
            font-family: 'Akrobat', sans-serif;

            overflow: hidden;
            .dispatch-code {
                width: fit-content;
                height: fit-content;
                background-color: rgb(21, 114, 255);
                padding: .25vh;
                padding-left: .7vh;
                padding-right: .7vh;
                border-radius: .5vh;
                margin-top: .5vh;
                margin-left: .5vh;

            }

            .ueberschrift {
                width: 10vw;
                border-radius: .5vh;
                margin-top: .5vh;
                margin-left: .5vh;
                white-space: nowrap; 
                overflow: hidden;
                text-overflow: ellipsis;
            }

            .content {
                position: relative;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: start;
                height: 5vh;
                top: 1vh;
                gap: 1vh;

                .name, .position-text {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    left: 0;
                    height: 3vh;
                    width: 10vw;
                    margin: 0;
                    padding: 0;

                    
                    .icon {
                        width: 2vh;
                        height: 2vh;
                        font-size: 1.6vh;
                        display: flex;
                        justify-content: flex-start;
                        align-items: center;
                        margin-right: 1vh; 
                        margin-left: .5vh; 
                        color: rgba(255, 255, 255, 0.548);
                    }

                    .content-text{
                        position: relative;
                        padding: 0;
                        margin: 0;
                        left: -2vh;
                        height: 2vh;
                        display: flex;
                        justify-content: space-between;
                        // gap: 1vh;
                        text-align: center;
                        align-items: center;
                        font-size: 1.4vh;
                        width: 110%;
                        max-width: 110%;
                        left: 0;
                        color: rgba(255, 255, 255, 0.548);
                        span {
                            white-space: nowrap;
                        }
    
                    }
                }
            }
        }

        .infos-icons {
            position: absolute;
            right: 0;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            height: 100%;
            width: 4vh;
            background-color: rgba(0, 0, 0, 0.527);
            border-radius: 0 .5vh 0 .5vh;

            .icon {
                font-size: 2.4vh;
            }
            
        }
    }
}


@keyframes width {
  from {
    width: 100%
  }
  to {
    width: 0%
  }
}

.lista-move,
.lista-enter-active,
.lista-leave-active {
    transition: all .5s ease;
}

.lista-enter-from,
.lista-leave-to {
    opacity: 0;
}

.lista-leave-to {
    animation: width .4s linear;
}

// .lista-leave-active {
//     position: relative;
// }



</style>
