<template>
    <div class="chat">
        <div class="rectanlge"></div>
        <div class="messages">
            <transition-group name="list" tag="div">
                <div v-for="(msg) in filteredMessages" :key="msg">
                    <div class="message" v-show="msg.type === currentType">
                        <div class="user-time">
                            <div class="type">{{ msg.type }}</div>
                            <div class="user">{{ msg.user }}</div>
                            <div class="time">{{ msg.time }}</div>
                        </div>
                        <div class="content">{{ msg.message }}</div>
                    </div>
                </div>
            </transition-group>
        </div>
        <input ref="chatInput" type="text" v-model="inputMessage" @keydown.enter="sendMessage" placeholder="Gebe eine Nachricht ein" />
        <div class="types">
            <div v-for="(type, index) in types" :key="index" >
                <div class="type" @click="setCurrentType(type)" :class="{active:  currentType === type}">{{ type }}</div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'chat-hud',
  props: {
    currentUser: Object,
  },

  data() {
    return {
        inputMessage: '',
        currentType: 'OOC',
        selectedChat: null,
        emojiList: false,
        messages: [
            // { user: 'Peter blume', type: 'OOC', message: 'Hello World!', time: '22:00' },
            // { user: 'Gunter malz', type: 'OOC', message: 'Hello World!', time: '22:15' },
        ],
        types: [
            'OOC',
            'IC',
            'Organisation',
        ]
    }
  },
  computed: {
        filteredMessages() {
            return this.messages.filter(msg => this.shouldDisplayMessage(msg));
        }
    },
  methods : {
    shouldDisplayMessage(message) {
        if (message.type !== 'Organisation') {
            return true;
        } else {
            return this.currentUser.job === message.job;
        }
    },
    setCurrentType(type) {
        this.currentType = type
        const data = {
            label: "loadTypeMessages",
            type: type
        }

        this.fetchToClient(data)
    },
    sendMessage() {
        if (this.inputMessage.trim() !== '') {
            const currentTime = new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
            let data = {
                file: "chat",
                label: "sendMessage",
                user: this.currentUser.name,
                type: this.currentType,
                message: this.inputMessage,
                job: this.currentType === 'Organisation' ? this.currentUser.job : null,
                time: currentTime,
            }
            if (!this.inputMessage.trim().startsWith('/')) {
                this.messages.unshift(data);
            }
            this.fetchToClient(data)
            this.inputMessage = '';
        }
    },
    async fetchToClient(data) {
        try {
            /*global GetParentResourceName*/
            /*eslint no-undef: "error"*/
            const response = await fetch(`https://${GetParentResourceName()}/hud_sonix:fetchToClient`, {
                method: "post",
                body: JSON.stringify({
                    data: data,
                })
            });

            if (!response.ok) {
                throw new Error('Failed to fetch');
            }

            const responseData = await response.json();
            console.log('Response from server:', responseData);
        } catch (error) {
            console.log('Error fetching data:', error.message);
        }
    },
    handleKeyDown(event) {
      if (event.keyCode === 27) {
        const data = {
            file: "chat",
            label: "closeChat",
        }
        this.$refs.chatInput.blur();
        this.fetchToClient(data)
        this.$emit('close')
      }
    },
    message(evt) {
        const { action, ...data } = evt.data;

        const actions = {
            'pushMessages': () => {
                const newMessages = data.messages.filter(newMsg =>
                    !this.messages.some(existingMsg =>
                        existingMsg.user === newMsg.user &&
                        existingMsg.type === newMsg.type &&
                        existingMsg.message === newMsg.message
                    )
                );

                this.messages.push(...newMessages);
            },
        };

        const actionFunction = actions[action];
        if (actionFunction) {
            actionFunction();
        }
    },
  },
  mounted() {
    this.$refs.chatInput.focus();
    window.addEventListener('message', this.message)
    window.addEventListener('keydown', this.handleKeyDown)
  },
  unmounted() {
    window.removeEventListener('message', this.message)
    window.removeEventListener('keydown', this.handleKeyDown)
  }
}

</script>

<style lang="scss" scoped>
@import url('https://fonts.cdnfonts.com/css/akrobat');
@import url('https://fonts.cdnfonts.com/css/sf-pro-display');

.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateY(20px); /* Verschiebung nach unten */
}

.list-move,
.list-enter-active,
.list-leave-active {
    transition: all .5s ease;
}

.list-enter-to,
.list-leave-from {
  opacity: 1;
  transform: translateY(0); /* Zurück zur normalen Position */
}

.chat {
    font-family: 'Akrobat', sans-serif;
    user-select: none;
    position: absolute;
    width: 20vw;
    height: 35.95vh;
    top: 5vh;
    left: 1vh;
    .rectanlge {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.123);
        filter: blur(6vh);
        z-index: -1;
    }

    .messages {
        height: 75%;
        width: 100%;

        display: flex;
        flex-direction: column-reverse;
        overflow: hidden;
        // gap: 1vh;
        overflow-y: scroll;
        &::-webkit-scrollbar {
            display: none;
        }
        .message {
            border-radius: .5vh;
            background: rgba(0, 0, 0, 0.582);
            box-shadow: 0.1852vh 0.1852vh 0.463vh 0.1852vh rgba(0, 0, 0, 0.336);
            color: white;
            height: fit-content;
            margin-top: -.8vh;
            transition: .5s all;
            .user-time {
                position: relative;
                left: .5vh;
                top: .5vh;
                padding-top: .5vh;
                display: grid;
                grid-template-columns: auto auto 1fr; /* Erste Spalte für .type, zweite für .user, dritte für .time */
                align-items: center;
                .type {
                    font-family: 'Akrobat-Bold', sans-serif;
                    position: relative;
                    left: 0;
                    padding-left: .4vh;
                    padding-right: .4vh;
                    padding-top: .2vh;
                    padding-bottom: .2vh;

                    min-width: 3vh;
                    background: rgb(0, 128, 255);
                    border-radius: .5vh;
                    text-align: center;
                    justify-content: center;
                    font-weight: 600;
                    margin-right: 1vh;
                    font-size: 1.2vh;
                }

                .user {
                    margin-right: 1vh;
                    font-weight: 500;
                    font-family: 'Akrobat-Bold', sans-serif;
                    font-size: 1.5vh;
                    font-weight: 500;
                }

                .time {
                    position: relative;
                    justify-self: end;
                    color: rgba(255, 255, 255, 0.611);
                    font-size: 1.4vh;
                    margin-right: 1vh;
                }
            }


            .content {
                position: relative;
                top: .70vh;
                left: .5vh;
                color: rgba(255, 255, 255, 0.815);

                flex-grow: 1; /* Take up remaining space */
                padding-top: .5vh;
                padding-bottom: 1.5vh;
                margin-bottom: 1vh;
                font-size: 1.7vh;

            }

        }

    }
    
    input {
        position: relative;
        width: 97%;
        height: 3vh;
        border: none;
        border-radius: .5vh;
        background: rgba(0, 0, 0, 0.541);
        box-shadow: 0.1852vh 0.1852vh 0.463vh 0.1852vh rgba(0, 0, 0, 0.336);
        color: white;
        padding-bottom: .5vh;
        padding-top: .5vh;
        font-size: 1.3vh;
        padding-left: 1vh;
        margin-top: 1vh;
        &::placeholder {
            position: relative;
            left: .25vh;
            color: white;
        }
        &:focus {
            outline: none;
        }
    }
    .types {
        display: flex;
        margin-top: .51vh;
        gap: .25vh;
        .type {
            transition: .2s all;
            cursor: pointer;
            color: #fff;
            min-width: 4vh;
            padding: .3vh;
            background: rgba(0, 0, 0, 0.486);
            box-shadow: 0.1852vh 0.1852vh 0.463vh 0.1852vh rgba(0, 0, 0, 0.336);
            text-align: center;
            border-radius: .65vh;
            font-family: 'Akrobat-Bold', sans-serif;
            font-size: 1.4vh;
            font-weight: 600;

            &:hover, &.active {
                background: rgb(1, 104, 207);
            }
        }
    }
}
</style>