<template>
  <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
  <div class="hud">
    <Chat v-if="show.chat" :currentUser="currentUser" @close="show.chat = false"/>
    <PinnedNotify/>
    <Notifys/>
    <Speedometer v-if="show.Speedometer" />
    <foodbars :Data="hud.foodBarData"/>
    <settings v-if="show.settings"/>

    <div class="top-right-middle">
      <dispatch/>
    </div>

    <transition-group name="list" tag="div">
      <div class="middle-center">
        <postit v-if="show.postit" key="postit" @hidePostIT="show.postit = false" />
        <modalWindow v-else-if="show.modalWindow" key="modalWindow" @hide="show.modalWindow = false"/>
        <progressbar key="progressbar"/>
      </div>

      <div class="bottom-center">
        <itemNotify key="itemNotify"/>
        <helpnotify v-if="show.helpnotify" key="helpnotify" :Data="hud.helpnotify"/>
        <div class="safezone" v-if="show.safeZone">
          <div class="rectangle"></div>
          <Icon class="icon" icon="iconamoon:shield-yes-thin" />
          <p>SafeZone</p>
        </div>
      </div>
    </transition-group>



    <div class="position">
      <div class="rectangle"></div>

      <Icon class="icon" icon="foundation:marker" />
      <div class="text">
        <p>{{ position.street }}</p>
        <span>{{ position.zone }}</span>
      </div>
    </div>

    <div class="top-right">
      <div class="server-infos">
        <div class="rectangle"></div>
        <div class="top">

          <div class="item time">
            {{ hud.currentTime }}
          </div>
          <div class="item players">
            <Icon class="icon" icon="material-symbols:diamond" />
            <div class="box">
              {{ hud.onlinePlayer}}
            </div>
          </div>
          <div class="item playerId">
            <Icon class="icon" icon="clarity:user-solid" />
            <div class="box">
              {{ hud.playerId }}
            </div>
          </div>
        </div>
        <div class="middle">
          <div class="item voice">
            <Icon class="icon" icon="icon-park-outline:voice" />
            <div class="box">
              {{ 
                  hud.mic.range === 1 ? 'Flüstern' : 
                  hud.mic.range === 2 ? 'Reden' : 
                  hud.mic.range === 3 ? 'Laut Reden' : 
                  'Schreien'
              }}
            </div>
          </div>
          <div class="item radio">
            <Icon class="icon" icon="fluent:walkie-talkie-20-filled" />
            <div class="box">
              {{ hud.mic.radio }}
            </div>
          </div>
        </div>
        <div class="bottom">
            <div class="item">
                <div class="star" v-for="i in 5" :key="i">
                  <Icon :icon="i <= hud.stars ? 'bi:star-fill' : 'bi:star'" />
                </div>
            </div>
        </div>
      </div>
    </div>

    <div class="middle-right">
      <div v-show="show.pistol" class="pistol">
