        <div class="name">
          {{ hud.pistol.name }}
        </div>
        <div class="ammo">
          <div class="in">
            <p v-if="hud.pistol.in < 10"> <span>00</span>{{ hud.pistol.in }}</p>
            <p v-if="hud.pistol.in < 100 && hud.pistol.in >= 10"> <span>0</span>{{ hud.pistol.in }}</p>
            <p v-if="hud.pistol.in >= 100">{{ hud.pistol.in }}</p>
          </div>
          <div class="out">
            <p v-if="hud.pistol.out < 10"> <span>00</span>{{ hud.pistol.out }}</p>
            <p v-if="hud.pistol.out < 100 && hud.pistol.out >= 10"> <span>0</span>{{ hud.pistol.out }}</p>
            <p v-if="hud.pistol.out >= 100">{{ hud.pistol.out }}</p>
            <Icon class="icon" icon="mdi:ammunition" />
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import Chat from './components/chat.vue'
import PinnedNotify from './components/pinnednotifys.vue'
import Notifys from './components/notifys.vue'
import Speedometer from './components/speedometer.vue'
import foodbars from './components/foodbars.vue'
import settings from './components/settings.vue'
import progressbar from './components/progressbar.vue'
import helpnotify from './components/helpnotify.vue'
import dispatch from './components/dispatch.vue'
import postit from './components/postit.vue'
import itemNotify from './components/itemNotify.vue'
import modalWindow from './components/modalWindow.vue'

import { Icon } from '@iconify/vue';

/*global GetParentResourceName*/
/*eslint no-undef: "error"*/

export default {
  name: 'Sonix-Hud',
  components: {
    Chat,
    Icon,
    PinnedNotify,
    Notifys,
    Speedometer,
    foodbars,
    settings,
    progressbar,
    helpnotify,
    dispatch,
    postit,
    itemNotify,
    modalWindow
  },
  data() {
    return {
      hud: {
        onlinePlayer: 1,
        playerId: 2,
        mic: {
          radio: 32,
          range: 1,
          muted: false,
        },
        pistol: {
          name: "Kombat Pistole",
          inside: 100,
          out: 50,
        },
        stars: 0,
        currentTime: '',
        foodBarData: [],
        notifys: [],
        extraData: null,
        helpnotify: {
          key: "E",
          label: "Test label",
          message: "Test Message",
        },
      },
      show: {
        settings: false,
        safeZone: false,
        postit: false,
        helpnotify: false,
        modalWindow: false,
        chat: false,
        Speedometer: false,
        pistol: false,
      },
      currentUser: {
        name: 'Two Face',
        job: ''
      },
      position: {
        street: "MADE BY",
        zone: "TWOFACE"
      },
    }
  },
  methods : {
    updateTime() {
      const date = new Date();
      const hours = this.padZero(date.getHours());
      const minutes = this.padZero(date.getMinutes());
      this.hud.currentTime = `${hours}:${minutes}`;
    },
    padZero(value) {
      return value < 10 ? '0' + value : value;
    },
    message(evt) {
        const { action, ...data } = evt.data;

        const actions = {
            'setmoney': () => {
                this.money = data.money;
                this.blackmoney = data.blackmoney;
            },
            'setidentifier': () => {
                this.hud.playerId = data.id;
            },
            'setonlinePlayer': () => {
              this.hud.onlinePlayer = data.identifier;
            },
            'setStars': () => {
                this.hud.stars = data.stars;
            },
            'micStates': () => {
                this.hud.mic.range = data.range;
                this.hud.mic.muted = data.muted;
            },
            'funkStates': () => {
              this.hud.mic.radio = data.radio;
            },            
            'showpistol': () => {
                this.show.pistol = data.boolean                
            },
            'setPistol': () => {
              
                this.hud.pistol = data.pistol;
            },
            'setplayers': () => {
                this.players = data.players;
            },
            'streets': () => {
                this.position.zone = data.crossingRoad;
                this.position.street = data.street;
            },
            'showModalWindow': () => {
              this.show.modalWindow = data.boolean;
            },
            'showPostit': () => {
                this.show.postit = data.boolean;
            },
            'showsafeZone': () => {
              this.show.safeZone = data.boolean;
            },
            'showChat': () => {
              this.show.chat = data.boolean;
            },
            'setCurrentUser': () => {
              this.currentUser = data.currentUser;
            },
            'showhelpnotify': () => {
                this.show.helpnotify = data.boolean;
                this.hud.helpnotify = data.helpnotify
            },
            'inVeh': () => {
                this.show.Speedometer = data.boolean;
            },
            'setFoodBars': () => {
              this.hud.foodBarData = data;
            },            
        };

        const actionFunction = actions[action];
        if (actionFunction) {
            actionFunction();
        }
    },
    fetchToClient(data) {
      fetch(`https://${GetParentResourceName()}/hud:fetchToClient`, {
        method: "post",
        body: JSON.stringify({
            data: data,
        })
      }) 
    },
  },
  watch: {

  },
  mounted() {
    this.updateTime();
    setInterval(this.updateTime, 1000);
    window.addEventListener('message', this.message)

  },
  unmounted() {
    window.removeEventListener('message', this.message)
    clearInterval(this.updateTime);
  }
}   
</script>


