<template>
    <div class="foodbars">
        <div class="rectangle"></div>
        <div v-for="content in progressBars" :key="content" class="content-boxes">
                <div class="outer-content">
                    <svg class="progress-circle-bg" viewBox="0 0 100 100">
                        <circle cx="50" cy="50" r="45" fill="none" :stroke="content.color" stroke-width="8"/>
                    </svg>
    
                    <svg class="progress-circle" viewBox="0 0 100 100">
                        <circle cx="50" cy="50" r="45" fill="none" :stroke="content.color" :stroke-width="8" :stroke-dasharray="content.progress * 2.83 + ' 283'"/>
                    </svg>
                    <div class="inner-content2">
                        <Icon class="icon" :icon="content.icon" :color="content.color" :style="{ background: 'rgba(' + hexToRgb(content.color).r + ', ' + hexToRgb(content.color).g + ', ' + hexToRgb(content.color).b + ', 0.455)' }" />
                    </div>
                </div>

        </div>
    </div>
</template>

<script>
import { Icon } from '@iconify/vue';

export default {
    name: 'foodbars-component',
    components: {
        Icon
    },
    props: {
        Data: Object
    },
    data() {
        return {
            progressBars: [
                {
                    type: 'leben',
                    icon: 'mdi:heart',
                    progress: 0,
                    color: "#ff3030"
                    
                },
                {
                    type: 'shield',
                    icon: 'mdi:shield',
                    progress: 0,
                    color: "#304cff"
                },
                {
                    type: 'food',
                    icon: 'mdi:hamburger',
                    progress: 0,
                    color: "#f59e42"
                },
                {
                    type: 'drink',
                    icon: 'mdi:cup-water',
                    progress: 0,
                    color: "#0363ff"
                },
                {
                    type: 'blitz',
                    icon: 'mdi:flash',
                    progress: 0,
                    color: "#ffd9ed"
                }
            ]
        };
    },
    // mounted() {
    //     console.log("test2323", this.Data)
    //     console.log("FoodData", JSON.stringify(this.Data))
    // },
    watch: {
        Data: {
            deep: true,
            handler() {
                this.updateBars();
            }
        }
    },
    methods: {
        hexToRgb(hex) {
            hex = hex.replace(/^#/, '');

            if (hex.length === 3) {
                hex = hex.replace(/(.)/g, '$1$1');
            }

            var bigint = parseInt(hex, 16);
            var r = (bigint >> 16) & 255;
            var g = (bigint >> 8) & 255;
            var b = bigint & 255;

            return { r, g, b };
        },
        updateBars() {
            // Kopiere das Data-Objekt in ein neues Objekt
            let newData = Object.assign({}, this.Data);

            // Schleife durch das progressBars-Array
            for (let i = 0; i < this.progressBars.length; i++) {
                const bar = this.progressBars[i];

                // Aktualisiere den Wert im neuen Objekt
                bar.progress = newData[bar.type]; // Aktualisiere den Fortschritt basierend auf den neuen Daten
            }

        }

    }
};
</script>

<style lang="scss" scoped>

.foodbars {
    position: absolute;
    right: 3.75vh;
    bottom: 1.28vh;
    display: flex;
    gap: 1vh;

    .rectangle {
      position: absolute;
      width: 100%;
      height: 100%;
      background: black;
      filter: blur(7vh);
    }

    .content-boxes {
        position: relative;
        height: 4vh;
        width: 4vh;

        .outer-content {
            position: absolute;
            height:  4.5vh;
            width: 4.5vh;
            padding: 0;
            margin: 0;
            transition: .5s all;
            .progress-circle-bg,
            .progress-circle {
                position: absolute;
                top: 0;
                left: 0;
                transform: rotate(-90deg);
    
                circle {
                    fill: none;
                }
            }
    
            .progress-circle-bg circle {
                opacity: .5;
                stroke-width: 3;
            }
    
            .progress-circle circle {
                stroke-width: 4;
                stroke-linecap: round;
                transition: stroke-dasharray 0.3s ease;
            }
        }


        .inner-content2 {
            position: relative;
            width: 100%;
            height: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            border-radius: 100%;

            .icon {
                position: absolute;
                width: 2.5vh;
                height: 2.5vh;
                transform: translate(-50%, -50%);
                left: 50%;
                top: 50%;
                color: white;
                border-radius: 50%;
                padding: .45vh;
            }
        }
    }
}

</style>
